$main-font: "Roboto";

.Footer{
  background-color: #161415;
  padding-top: 50px;
  padding-bottom: 50px;

}
.subTitle{
  display: block;
  color: white;
  font-size: 1.5em;
  font-weight: 600;
  margin-bottom: 10px;
}
.contentWrapper{
  max-width: 100vw;
  width: 1000px;
  margin: auto;
}

@media (min-width: 1024px) {
  .socialNetworks {
    display: inline-block;
    height: 150px;
  }
  .map {
    height: 150px;
    display: inline-block;
    padding-left: 100px;
  }
  .support {
    height: 150px;
    display: inline-block;
    padding-left: 100px;
  }
  .contacts {
    height: 150px;
    display: inline-block;
    padding-left: 100px;
  }
}

@media (max-width: 1024px) {
  .socialNetworks {
    text-align: left;
    margin: auto;
    width: 300px;
    padding-bottom: 30px;

  }
  .map {
    text-align: left;
    margin: auto;
    width: 300px;
    padding-bottom: 30px;
  }
  .support {
    text-align: left;
    margin: auto;
    width: 300px;
    padding-bottom: 50px;

  }
  .contacts {
    padding-bottom: 30px;
    text-align: left;
    margin: auto;
    width: 300px;

  }
}

@media (min-width: 1024px) {
  .entery {
    cursor: pointer;
    margin-bottom: 5px;
    display: block;
    color: white;
    font-size: 1.0em;
    font-weight: 600;

  }
}
@media (max-width:1024px ) {
  .entery {
    cursor: pointer;
    margin-bottom: 5px;
    display: block;
    color: white;
    font-size: 0.9em;
    font-weight: 600;

  }
}
  
